import React, { Component } from 'react';

export class SearchResult extends Component {

  handleClick = (e) => {
    e.stopPropagation();
  }

  render() {
    let {title, body, isSelected} = this.props;
    let classNames = ['result']
    if( isSelected ) {
      classNames.push('selected')
    }

    return (
      <a href={this.props.url} tabIndex="0" onClick={this.handleClick}>
        <div className={ classNames.join(' ') }>
          <h1 dangerouslySetInnerHTML={{__html: title}} />
          <p dangerouslySetInnerHTML={{__html: body}} />
        </div>
      </a>
    )
  }
}

export default class SearchResults extends Component {
  render() {
    const {results, selectedIndex, scope} = this.props;
    const content = results.map((result, i) => {
      return (
        <SearchResult {...result}
                      key={'result-'+i}
                      isSelected={selectedIndex === i} />
      )
    })

    return (
      <div className='results'>
        <div className='result resultsScope'>
          <p onClick={ this.props.onTipsClick }>
            (<span className="tipsLink">Advanced search</span>)
          </p>
          Searching within <span>{ scope }</span>
        </div>
        {content}
      </div>
    );
  }
}


