import debounce from './debounce';
import React, { Component } from 'react';

export default class FocusManager extends Component {
  constructor(props) {
    super(props);

    this.state = { focused: null }

    this._triggerCallbacks = debounce(this._triggerCallbacks.bind(this), 200)
  }

  handleFocus = (e) => {
    this.setState({ focused: e.target })
    this._triggerCallbacks()
  }

  handleBlur = (e) => {
    if ( e.target === this.state.focused ) {
      this.setState({ focused: null })
      this._triggerCallbacks()
    }
  }

  _triggerCallbacks() {
    if ( this.state.focused ) {
      this.props.onFocus( this.state.focused )
    } else {
      this.props.onBlur()
    }
  }

  render = () => {
    let handlers = {onFocus: this.handleFocus, onBlur: this.handleBlur, onMouseDown: this.handleMouseDown, onMouseUp: this.handleMouseUp, onTouchStart: this.handleTouchStart, onTouchEnd: this.handlTouchEnd};
    return (
      <div {...this.props} {...handlers}>
        {this.props.children}
      </div>
    )
  }

}





