import React, { Component } from 'react';

export default class SearchControls extends Component {
  render() {
    let {totalCount, from, count, getNext, getPrev} = this.props
    if (!count) {
      return null;
    }
    return (
      <nav>
        <div >
          {from + 1} to {from + count} of {totalCount} results
        </div>
        <button disabled={from <= 0}
                name="btn_prev"
                onClick={getPrev}>
          &lt; previous
        </button>
        <button disabled={totalCount <= (from + count)}
                name="btn_next"
                onClick={getNext}>
          next &gt;
        </button>
      </nav>
    )
  }
}



