export function findPaths() {
  const paths = []
  const elements = document.getElementsByClassName('ancestors')
  const breadcrumbs = elements[0].children
  for (var i = 0; i < breadcrumbs.length; i++) {
    let x = breadcrumbs[i]
    let path = x.attributes['data-search-path']
    let heading = x.attributes['data-search-heading']
    let isDefault = x.attributes['data-search-default']

    let display = heading ? heading.value : ''
    if ( !display ) {
      display = x.innerText || ''
      display = display.replace('↪ ', '')
    }

    paths.push({
      key: path ? path.value : 'library',
      display,
      isDefault: isDefault != undefined ? isDefault.value : 'false'
    })
  }

  return paths
}



