import * as constants from '../constants'
import { findPaths } from './paths' 
import PropTypes from 'prop-types'
import React from 'react'

export default class SelectPath extends React.Component {
  constructor(props) {
    super(props)

    this.state= {
      paths: [],
      selected: -1,
      default: ''
    }

    // Key/function map
    this.keyMap = {}
    this.keyMap[constants.VK_ESCAPE] = this.props.onToggle
    this.keyMap[constants.VK_UP] = this._nav.bind(this, -1)
    this.keyMap[constants.VK_DOWN] = this._nav.bind(this, 1)
    this.keyMap[constants.VK_ENTER] = this._chooseSelectedIndex.bind(this)
    this.keyMap[constants.VK_RETURN] = this._chooseSelectedIndex.bind(this)
    this.keyMap[constants.VK_TAB] = this._chooseSelectedIndex.bind(this)

    // event handlers
    this._onKeyDown = this._onKeyDown.bind(this)
  }

  componentDidMount() {
    const paths = findPaths()

    let selected_i = paths.length-1;
    for(var i=0; i<=selected_i; i++) {
      if(paths[i].isDefault == 'true') {
        selected_i = i;
      }
    }

    this.setState({
      paths,
      selected: selected_i
    })
    this.props.onSelected(paths[selected_i]);
  }

  render() {
    const ulClass = this._isVisible ? 'dsf' : 'hide' 

    return (
      <div className="select-path-container">
        <button aria-label="Choose which path will be searched"
                name="pathMenu"
                onClick={ this.props.onToggle }
                onKeyDown={this._onKeyDown}
                title="Select search scope"
        >
        <span className="caret-down"></span>
        </button>
        <ul className={ ulClass }>
          {
            this.state.paths.map( ( x, i ) =>
                <li className={ (i === this.state.selected) ? 'selected' : ''}
                    key={x.key}
                    onClick={ () => this._onSelected(i) }
                >
                  {x.display}
                </li>
              )
          }
        </ul>
      </div>
    )
  }

  // --------------------------------------------------------------------------
  // Properties

  get _isVisible() {
    return this.props.showScope
  }

  // --------------------------------------------------------------------------
  // Event Handlers

  _onSelected(selected) {
    this.setState({
      selected,
    }, function() {
      this.props.onSelected(this.state.paths[selected]);
    });
    
  }

  // --------------------------------------------------------------------------
  // Key Helpers

  _onKeyDown(event) {
    const handler = this.keyMap[event.which]
    if (handler) {
      handler(event)
    }
  }

  _chooseSelectedIndex(event) {
    if (this.state.paths.length === 0) {
      return;
    }

    let idx = this.state.selected
    if (idx === -1) {
      idx = 0
    }

    this._onSelected(idx)
    event.preventDefault()
  }

  _nav(delta, event) {
    event.preventDefault()

    const max = this.state.paths.length
    if (max === 0) {
      return
    }

    // Clamp the new index
    let newIndex = this.state.selected + delta
    if (newIndex < 0) {
      newIndex = 0
    }
    if (newIndex >= max) {
      newIndex = max - 1
    }

    this.setState({selected: newIndex})
  }
}


// ----------------------------------------------------------------------------
// Meta

SelectPath.propTypes = {
  onSelected: PropTypes.func.isRequired
}



