import './search.css'
import * as constants from '../constants'
import debounce from '../debounce'
import FocusManager from '../FocusManager'
import React, { Component } from 'react'
import SearchControls from './SearchControls'
import SearchResults from './SearchResults'
import SelectPath from './SelectPath'
import ReactDOM from 'react-dom'

// ----------------------------------------------------------------------------
// Constants

const EMPTY = 'EMPTY'
const WAITING = 'WAITING'
const NO_RESULTS = 'NO_RESULTS'
const RESULTS = 'RESULTS'
const SHOW_TIPS = 'SHOW_TIPS'

const DEFAULT_STATE = {
  from: 0,
  query: '',
  results: [],
  searchWithin: 'library',
  selectedIndex: -1,
  showTips: false,
  showScope: false,
  totalCount: 0,
  waiting: 0
}

// The search tips
const advancedTips = [
  { symbol: '+', example: 'fire +escape',
    means: 'Find matches that contain "fire" and "escape"'
  },
  { symbol: '|', example: 'fire | escape',
    means: 'Find matches that contain "fire" or "escape"'
  },
  { symbol: '-', example: 'fire -escape',
    means: 'Find matches that contain "fire" but not "escape"'
  },
  { symbol: '"', example: '"fire escape"',
    means: 'Find matches that contain the exact term "fire escape"'
  },
  { symbol: '*', example: 'fire*',
    means: 'Find matches that start with "fire", like "firearms" or "fireman"'
  },
  { symbol: '~N', example: '"fire stairway"~4',
    means: 'Find matches where "fire" is within 4 words of "stairway"'
  },
  { symbol: '^', example: 'fire^2 escape',
    means: 'Matches that contain "fire" are twice as important as "escape" matches'
  }
]

// ----------------------------------------------------------------------------
// Classes

export class SearchBox extends Component {
  render() {
    const placeholder = this.props.searchSection ?
      `search in ${this.props.searchSection}` :
      'search the code...'

    return (
      <input type="text"
             aria-label="search the code for a section or phrase"
             autoComplete="off"
             name="search"
             onChange={this.props.onChange}
             onKeyDown={this.props.onKeyDown}
             ref="searchbox"
             placeholder={placeholder}
             value={this.props.query} />
    )
  }
}

export default class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {...DEFAULT_STATE, focused: false};

    this.inFocusGroup = ['search', 'searchTips', 'btn_prev', 'btn_next']

    // Render/Phase Map
    this.renderMap = {
      EMPTY: this._renderEmpty.bind(this),
      WAITING: this._renderWaiting.bind(this),
      NO_RESULTS: this._renderNoResults.bind(this),
      RESULTS: this._renderResults.bind(this),
      SHOW_TIPS: this._renderTips.bind(this)
    }

    // Key/function map
    this.keyMap = {}
    this.keyMap[constants.VK_ESCAPE] = this._keyCancel.bind(this)
    this.keyMap[constants.VK_UP] = this._nav.bind(this, -1)
    this.keyMap[constants.VK_DOWN] = this._nav.bind(this, 1)
    this.keyMap[constants.VK_LEFT] = this.prevResults.bind(this)
    this.keyMap[constants.VK_RIGHT] = this.nextResults.bind(this)
    this.keyMap[constants.VK_ENTER] = this._chooseSelectedIndex.bind(this)
    this.keyMap[constants.VK_RETURN] = this._chooseSelectedIndex.bind(this)
    this.keyMap[constants.VK_TAB] = this._chooseSelectedIndex.bind(this)

    this._onKeyDown = this._onKeyDown.bind(this)

    this._makeSearch = debounce(this.__makeSearch, 150)
  }

  componentDidMount() {
    var query = window.location.hash;
    if (query != '' && query.startsWith('#!?')) {
      this.parseURLParams(query);
    }
  }

  render() {
    // determine the phase
    let phase = EMPTY
    if( this.state.showTips ) {
      phase = SHOW_TIPS
    }
    else if( this.state.showScope) {
      phase == EMPTY
    }
    else if( this.state.waiting > 0 ) {
      phase = WAITING
    }
    else if( this.state.results.length > 0 ) {
      phase = RESULTS
    }
    else if( this.state.query.length > 0 && this.state.results.length === 0) {
      phase = NO_RESULTS
    }

    let btnClass = 'tips-hint icon-info'
    if (this.state.showTips && this.state.focused ) {
      btnClass += ' active'
    }

    return (
      <FocusManager onFocus={this.handleFocus} onBlur={this.handleBlur}>
          <SelectPath onSelected={this._selectPath.bind(this)} name="selectPath" showScope={ this.state.showScope } onToggle={ () => { this._onToggleScope() }}/>
          <SearchBox onChange={this.handleQueryChange}
                      onKeyDown={this._onKeyDown}
                      searchSection={this.state.searchSection}
                      query={this.state.query}
                      ref="searchInput" />
        <button className={ btnClass }
                name="searchTips"
                onClick={ this._onToggleTips.bind(this) }
                title="Explain advanced search symbols"
        >
        </button>
        { this.state.focused ? this.renderMap[phase]() : null }
      </FocusManager>
    );
  }

  handleFocus = (x) => {
    // Is the focus being set on one of the controls in our group?
    const inGroup = this.inFocusGroup.indexOf(x['name']) !== -1;
    this.setState({ focused: inGroup });
  }
  handleBlur = () => {
    this.setState({ showScope: false });
    this.setState({focused: false});
  }
  // --------------------------------------------------------------------------
  // Search Methods

  parseURLParams(hash) {
    let hashSplit = hash.substring(3).split('&');
    let newState = { query: this.state.query, searchWithin: this.state.searchWithin, from: this.state.from };
    for(var i=0; i<hashSplit.length; i++) {
      var paramSplit = hashSplit[i].split('=');
      if(paramSplit.length == 2) {
        let name = decodeURIComponent(paramSplit[0]);
        let value = decodeURIComponent(paramSplit[1]);

        if (name == 'query') {
         newState['query'] = value;
        } else if (name == 'path') {
          newState['searchWithin'] = value;
        } else if (name == 'from') {
          let f = parseInt(value);
          if(!isNaN(f)) {
            newState['from'] = f;
          }
        }
      }
    }
    this.setState(newState, function () {
      window.location.hash = '';
      this.makeSearch(this.state.query, this.state.from);
    })
  }


  handleQueryChange = (event) => {
    var query = event.target.value;
    this.setState({query: query, from: 0});
    this.makeSearch(query);
  }

  nextResults = () => {
    this.setState({ from: this.state.from + 5 }, function () {
      this.makeSearch(this.state.query, this.state.from);
    });
  }

  prevResults = () => {
    this.setState({ from: Math.max(this.state.from - 5, 0) }, function() {
      this.makeSearch(this.state.query, this.state.from);
    });
  }

  makeSearch = (query, from) => {
    if (query.length >= this.props.minSearchLength) {
      this.focusInput();
      this._makeSearch(query, from);
    }    
  }

  __makeSearch = (query, from) => {
    from = from || 0;
    var xhr = new XMLHttpRequest();
    this.setState({waiting: this.state.waiting + 1});
    xhr.open('POST', this._buildApiUrl(window));
    xhr.setRequestHeader('Content-Type', 'application/json');
    xhr.onload = this.handleSearchResponse;
    xhr.from = from;
    xhr.send(JSON.stringify({
        'file': 'search', 
        'params': {
            'query_string': query,
            'from': from,
            'path': this.state.searchWithin
        },
    }));
  }

  _buildQueryObject = (theWindow, query, from, path) => {
    let file
    const currHost = theWindow.location ? theWindow.location.hostname : '' 

    if ( currHost === 'beta.code.dccouncil.us' ) {
      file = 'search-beta'
    }
    else if ( currHost === 'development.code.dccouncil.us' ) {
      file = 'search-development'
    }
    else if ( currHost === 'localhost' ) {
      file = 'search-development'
    }
    else {
      file = 'search'
    }

    const queryObject = {
      'file': file, 
      'params': {
          'query_string': query,
          'from': from,
          'path': path,
      },
    }
    return queryObject
  }

  _buildApiUrl = (theWindow) => {
    let searchHost = theWindow.searchHost || 'https://search.code.dccouncil.us'
    let qs = theWindow.queryUrl || '/v1/search'
    const currHost = theWindow.location ? theWindow.location.hostname : '' 

    if ( currHost === 'beta.code.dccouncil.us' ) {
      qs += '/beta'
    }
    else if ( currHost === 'development.code.dccouncil.us' ) {
      qs += '/development'
    }
    else if ( currHost === 'localhost' ) {
      qs += '/development'
    }

    return searchHost + qs
  } 

  handleSearchResponse = (event) => {
    let xhr = event.target;
    let newState = {waiting: Math.max(this.state.waiting - 1, 0)}
    newState.selectedIndex = -1
    if (xhr.status === 200) {
      let results = JSON.parse(xhr.responseText).hits || {};
      newState.totalCount = results.total || 0;
      results = results.hits || [];
      newState.from = xhr.from;
      newState.results = results.map((result) => ({
        title: (result.highlight.title || [result._source.title])[0],
        body: result.highlight.body== undefined ? '' : result.highlight.body[0],
        url: result._source.url + '#!?query=' + encodeURIComponent(this.state.query.trim()) + '&path=' + encodeURIComponent(this.state.searchWithin) + '&from=' + this.state.from
      }));
    }
    this.setState(newState);
  }

  _selectOption(index) {
    window.location.assign(this.state.results[index].url)
  }

  _selectPath(path) {
    this.setState({
      searchWithin: path.key,
      searchSection: path.display,
      showScope: false
		});
		if (this.refs.searchInput != null) {
			this.makeSearch(ReactDOM.findDOMNode(this.refs.searchInput).value);
		}
		// this.makeSearch(query);
  }

focusInput() {
  if (this.refs.searchInput != null) {
    ReactDOM.findDOMNode(this.refs.searchInput).focus();
  }
}

  // --------------------------------------------------------------------------
  // Key Helpers

  _onKeyDown(event) {
    const handler = this.keyMap[event.which]
    if (handler) {
      handler(event)
    }
  }

  _chooseSelectedIndex(event) {
    if (this.state.results.length === 0) {
      return;
    }

    let idx = this.state.selectedIndex
    if (idx === -1) {
      idx = 0
    }

    this._selectOption(idx)
    event.preventDefault()
  }

  _keyCancel(event) {
    event.preventDefault()
    this.setState({...DEFAULT_STATE})
  }

  _nav(delta, event) {
    event.preventDefault()

    const max = this.state.results.length
    if (max === 0) {
      return
    }

    // Clamp the new index
    let newIndex = this.state.selectedIndex + delta
    if (newIndex < 0) {
      newIndex = 0
    }
    if (newIndex >= max) {
      newIndex = max - 1
    }

    this.setState({selectedIndex: newIndex})
  }

  // --------------------------------------------------------------------------
  // Tips

  _onToggleTips() {
    this.setState({
      showTips: !this.state.showTips,
		});
		this.focusInput();
  }

  // --------------------------------------------------------------------------
  // Change search scope dropdown

  _onToggleScope() {
    this.setState({
      showScope: !this.state.showScope,
		});
  }

  // --------------------------------------------------------------------------
  // Render Helpers

  _renderEmpty() {
    return null
  }

  _renderWaiting() {
    return (
      <div className="results">
        <p>searching...</p>
      </div>
    )
  }

  _renderNoResults() {
    return (
      <div className="results">
        <div className="result resultsScope">
          <p onClick={ () => this._onToggleTips() }>
            (<span className="tipsLink">Advanced search</span>)
          </p>
          Searching within <span>{ this.state.searchSection }</span>
        </div>
        <p className="resultMessage">No results</p>
      </div>
    )
  }

  _renderResults() {
    let {results} = this.state;
    const controlProps = {
      count: results.length,
      from: this.state.from,
      getNext: this.nextResults,
      getPrev: this.prevResults,
      query: this.state.query,
      totalCount: this.state.totalCount
    }

    return (
      <div>
        <SearchResults results={results}
                       selectedIndex={this.state.selectedIndex} 
                       scope={this.state.searchSection} 
                       onTipsClick={() => this._onToggleTips()}/>
        <SearchControls {...controlProps}  />
      </div>
    );
  }

  _renderTips() {
    return (
      <div className="tips-container">
        <table>
          <thead>
            <tr className="changeScope">
              <th></th><th></th>
              <th name="scopeLink" 
                  onMouseDown={ (x) => { x.preventDefault(); } } 
                  onClick={ () => { this._onToggleScope(); this.setState({ showTips: false }) }}>
                  (<span>Narrow or broaden search</span>)
              </th>
            </tr>
            <tr>
              <th className="symbol">Symbol</th>
              <th className="example">Example</th>
              <th className="means">Means</th>
            </tr>
          </thead>
          <tbody>
            { advancedTips.map(x => (
              <tr key={x.symbol}>
                <td className="symbol">{ x.symbol }</td>
                <td className="example">{ x.example }</td>
                <td className="means">{ x.means }</td>
              </tr>
              ))
            }
          </tbody>
        </table>
      </div>
    )
  }
}

Search.defaultProps = {minSearchLength: 3}



